<template>
  <div class="body">
    <el-card class="box-card">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-row :gutter="20" style="margin-top:5px;">
          <el-col :span="8">
            <el-form-item label=""  placeholder="">
              <el-select v-model="ruleForm.customerName" @change="clientSelect2">
                <el-option
                  v-for="item in options.customerNameOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" placeholder="">
              <el-select v-model="ruleForm.brandName">
                <el-option
                  v-for="item in options.brandNameOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=""  placeholder="">
              <el-select v-model="ruleForm.clothingType">
                <el-option
                  v-for="item in options.clothingTypeOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20" style="margin-top: 30px; margin-bottom: 5px;">
          <el-col :span="10">
            <el-form-item label="">
              <el-input v-model="ruleForm.filePath"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-upload
              style="display: inline; margin-left: 10px;margin-right: 10px;"
              action="#"
              ref="fileupload"
              :show-file-list="false"
              :http-request="upLoadChange"
              :before-upload="beforeUpload"
            >
              <el-button type="primary">
                
                <i class="el-icon-upload el-icon--right"></i>
              </el-button>
            </el-upload>
          </el-col>
        </el-row>
        <el-row :gutter="20" style="margin-top: 30px; margin-bottom: 5px;">
          <div class="label" align="center" style="margin: 0 0 5px 0"></div>
          <el-table
            :data="ruleForm.tableData"
            max-height="400"
            border
            :stripe="true"
            :highlight-current-row="true"
          >
            <el-table-column prop="rangeName" label="" align="center"></el-table-column>
            <!-- <el-table-column prop="rangeAmount" label="" align="center"></el-table-column> -->
            <el-table-column prop="rangeNote" label="" align="center"></el-table-column>
          </el-table>
        </el-row>
        <el-row style="margin: 50px 0 10px 0">
          <el-col :span="3" :offset="10">
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          </el-col>
          <el-col :span="3">
            <el-button type="info" @click="cancel"></el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import XLSX from "xlsx";
export default {
  data() {
    return {
      rules: {
        customerName: [
          { required: true, message: "", trigger: "change" }
        ],
        brandName: [
          { required: true, message: "", trigger: "change" }
        ],
        clothingType: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      ruleForm: {
        customerName: "",
        brandName: "",
        clothingType: "",
        filePath: "",
        tableData: [ ]
      },
      options: {
        customerNameOptions: [ ],
        brandNameOptions: [ ],
        clothingTypeOptions: [ ]
      },
      fileList: []
    };
  },
  created: function() {
    var that = this;
  

    //
    that.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getCustomerName`)
      .then(response => {
        console.log("");
        var CustomerList = response.data;
        this.options.customerNameOptions = CustomerList;

      })
      .catch(error => {
        this.$message({
          message: "",
          type: "error"
        });
        // console.log("");
        // var CustomerList = [
        //   {
        //     id: 1,
        //     name: "A"
        //   },
        //   {
        //     id: 2,
        //     name: "B"
        //   },
        //   {
        //     id: 3,
        //     name: "C"
        //   }
        // ];
        // this.searchOptions.options.customerNameOptions = CustomerList;
        // this.ruleForm.options.customerNameOptions = this.searchOptions.options.customerNameOptions;
      });

    //
    that.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getClothingLevelName`)
      .then(response => {
        console.log("");
        var ClothingList = response.data;
        this.options.clothingTypeOptions = ClothingList;

      })
      .catch(error => {
        this.$message({
          message: "",
          type: "error"
        });
      });

   
  },
  methods: {
     clientSelect2() {
      this.ruleForm.brandName="";
      let list={
            customerId: this.ruleForm.customerName
          };
          console.log(list);
      this.$axios
        .get(`${window.$config.HOST}/baseInfoManagement/getBrandName`, {
          params: list
        })
        .then(response => {
          console.log(response.data);
          this.options.brandNameOptions = response.data;
        })
        .catch(error => {
          this.$message({
            message: "",
            type: "error"
          });
        });
    },


    ////////////// methods for xls /////////////
    readExcel(file) {
      // Excel
      const that = this;
      return new Promise(function(resolve, reject) {
        const reader = new FileReader();
        reader.onload = e => {
          try {
            // excel
            var data = e.target.result,
              workbook = XLSX.read(data, { type: "binary" });
          } catch (e) {
            reject("", e.message);
          }
          var fromTo = ""; // excel
          let sheetData = []; //
          let locations = []; // A1,B1,C1,A2,B3,C3...
          // 
          for (var sheet in workbook.Sheets) {
            if (workbook.Sheets.hasOwnProperty(sheet)) {
              let sheetInfos = workbook.Sheets[sheet];
              console.log("(sheetInfos): ", sheetInfos);
              fromTo = sheetInfos["!ref"]; // A1:B5
              console.log("(fromTo):" + fromTo);
              locations = that.getLocationsKeys(fromTo);
              console.log("locations:" + locations);
              var colMax = locations[0];
              console.log(": ", colMax);
              let rowData = {
                // 
                rangeName: "",
                rangeAmount: "",
                rangeNote: ""
              };
              for (let i = 1 + colMax; i < locations.length; i++) {
                //
                var value = "";
                try {
                  value = sheetInfos[locations[i]].v;
                  console.log(locations[i] + "value: ", value);
                } catch (e) {
                  value = "";
                  console.log(locations[i] + "");
                }
                if (i % colMax === 1) {
                  // rangeName
                  rowData.rangeName = value;
                  console.log("value: ", value);
                }
                if (i % colMax === 2) {
                  // rangeAmount
                  rowData.rangeAmount = value;
                  console.log("value: ", value);
                }
                if (i % colMax === 0) {
                  // rangeNote 
                  rowData.rangeNote = value;
                  sheetData.push(rowData);
                  rowData = {
                    //
                    rangeName: "",
                    rangeAmount: "",
                    rangeNote: ""
                  };
                }
              }
              resolve(true);
              console.log("sheetData", sheetData);
              that.ruleForm.tableData = sheetData;
            }
            console.log("");
          }
        };
        reader.readAsBinaryString(file);
      });
    },
    beforeUpload(file) {
      const that = this;
      return new Promise(function(resolve, reject) {
        that.readExcel(file).then(
          result => {
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isLt2M) {
              that.$message.error("2MB!");
            }
            if (isLt2M && result) {
              resolve("!");
            } else {
              reject(false);
            }
          },
          error => {
            that.$message.error(error);
            reject(false);
          }
        );
      });
    },
    upLoadChange(content) {
      this.$message.success("!");
    },
    getLocationsKeys(range) {
      // A1:B5 A1,B1...

      let sv = range.split(":");
      console.log("range", sv);
      let startString = sv[0];
      let endString = sv[1];

      let start = startString.substring(0, startString.length - 1); // 'A'
      console.log(": ", start);
      let end = endString.substring(0, endString.length - 1);
      console.log(": ", end);

      let rowMax = parseInt(endString.substring(1, endString.length)); // 
      console.log(": ", rowMax);

      let total = 0;
      for (let i = 0; i < end.length; i++) {
        total +=
          Math.pow(26, end.length - 1 - i) *
          (end.charCodeAt(i) - "A".charCodeAt(0) + 1);
        console.log("total: ", total);
      }
      let result = [];
      result.push(total);
      for (let rowNum = 1; rowNum <= rowMax; rowNum++) {
        for (let index = 0; index < total; index++) {
          result.push(this.getCharByNum(index) + rowNum.toString());
        }
      }
      return result;
    },
    getCharByNum(index) {
      let a = parseInt(index / 26); // 
      let b = index % 26; // 
      let returnChar = String.fromCharCode(b + 65); // 
      while (a > 0) {
        b = a % 26;
        a = parseInt(a / 26);
        // 
        returnChar = String.fromCharCode(b + 65 - 1) + returnChar;
      }
      return returnChar;
    },
    ////////////// methods for xls /////////////
    // 
    submitForm(formName) {
      const that = this;
      var RangeListAdd = [];
      this.ruleForm.tableData.forEach(element => {
        RangeListAdd.push({
          name: element.rangeName,
          customerId: this.ruleForm.customerName,
          brandId: this.ruleForm.brandName,
          clothingLevelId: this.ruleForm.clothingType,
          note: element.rangeNote
        });
      });


      this.$axios
        //
        .post(`${window.$config.HOST}/infoManagement/addRangeList`, 
          RangeListAdd
        )
        .then(response => {
        this.$router.push({
          name: `rangeManagement`
        });
          var ok = response.data;
          if (ok >= 0) {
            if(ok===RangeListAdd.length)
            {
this.$message({
              message: "",
              type: "success"
            });
            }
            else {
              this.$message({
              message: (RangeListAdd.length-ok)+"",
              type: "success"
            });
            }
            
          } else {
            this.$message({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(error => {
          this.$message({
            message: "",
            type: "warning"
          });
        });


    },
    // 
    cancel() {
      const that = this;
      console.log("");
      that.$router.push({
        name: `rangeManagement`
      });
    },
    // 
    findFile() {
      const that = this;
      console.log("");
    }
  }
};
</script>


<style lang="less" scoped>
.box-card {
  margin: 20px 50px;
  padding: 0 20px;
  .inputBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    .label {
      font-size: 14px;
      min-width: 75px;
      text-align: center;
    }
    .el-input {
      width: 300px;
      min-width: 75px;
    }
    .el-select {
      width: 300px;
      min-width: 75px;
    }
  }
  .block {
    padding: 30px 0;
    text-align: center;
  }
}
</style>


